@import '../assets/tokens';

.IuiButton {


    &:global(.MuiButton-root) {
        background-color: $orange-600;
        padding: 8px 12px;
        min-width: $button-min-width;
        color: #fff;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        border-radius: 6px;
        text-transform: capitalize;
        &:disabled {
            opacity: 50%; }

        &.primary {
            &:hover {
                background-color: $orange-700; }
            &:focus {
                background-color: $orange-600;
                box-shadow: 0px 1px 0px rgba(17, 24, 39, 0.1), 0px 0px 0px 2px #FFFFFF, 0px 0px 0px 4px #FDBA74; }
            &:active {
                background-color: $orange-800; }
            &:selected {
                background-color: $orange-900; } }

        &.secondary {
            background-color: #fff;
            color: $orange-600;
            border: 1px solid #D1D5DB;
            box-shadow: 0px 1px 0px rgba(17, 24, 39, 0.1);
            &:hover {
                background-color: #fff;
                box-shadow: 0px 2px 4px rgba(17, 24, 39, 0.15), 0px 1px 1px rgba(17, 24, 39, 0.1); }
            &:active {
                box-shadow: none; }
            &:focus {
                box-shadow: 0px 1px 0px rgba(17, 24, 39, 0.1), 0px 0px 0px 2px #FFFFFF, 0px 0px 0px 4px #FDBA74; } }

        &.neutral {
            background-color: #fff;
            color: black;
            border: 1px solid #D1D5DB;
            &:hover {
                background-color: #fff;
                box-shadow: 0px 2px 4px rgba(17, 24, 39, 0.15), 0px 1px 1px rgba(17, 24, 39, 0.1); }
            &:active {
                box-shadow: none; }
            &:focus {
                box-shadow: 0px 1px 0px rgba(17, 24, 39, 0.1), 0px 0px 0px 2px #FFFFFF, 0px 0px 0px 4px #FDBA74; } } } }
