@import '../assets/tokens';

.newclass {
    width: 500px;
    color: #000 !important; }

.wideButton {
    width: $button-min-width; }

.itltTextfield {
    background: #FFFFFF;
    border: 1px solid #D1D5DB;
    border-radius: 6px;
    height: 40px;
    width: 320px;
    padding: 0px 12px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    // line-height: 24px
    &:hover {
        border: 1px solid #6B7280; }
    &:active, &:focus {
        border: 1px solid #6B7280;
        box-shadow: 0px 0px 0px 4px #FFEDD5; } }
